.jumbotron--mb { margin-bottom: 3rem !important; }

@media only screen and (min-width: 992px) { .jumbotron--mb { margin-bottom: 4rem !important; } }

@media only screen and (min-width: 768px) { .jumbotron--catalog { min-height: 50vh; } }

.jumbotron__title { font-size: 2rem; color: #ffffff; font-weight: 700; text-transform: uppercase; margin-bottom: 1rem; }

@media only screen and (min-width: 992px) { .jumbotron__title { font-size: 3rem; } }

.jumbotron__descr { color: #eeeeee; max-width: 50rem; }

.jumbotron__subnav { background-color: #ffffff; box-shadow: 0 0 3px 5px rgba(0, 0, 0, 0.05); padding: 1.25rem 2rem; margin-left: -15px; margin-right: -15px; margin-top: 2rem; margin-bottom: -3rem; }

@media only screen and (min-width: 768px) { .jumbotron__subnav { margin-bottom: -4rem; margin-left: 0; margin-right: 0; } }

.jumbotron__list { overflow-x: scroll; display: flex; }

@media only screen and (min-width: 768px) { .jumbotron__list { overflow-x: visible; } }

.jumbotron__list a { font-size: 1rem; font-weight: 700; text-transform: uppercase; color: #333333; margin-right: 2rem; transition: all ease 0.3s; }

.jumbotron__list a:last-child { margin-right: 0; }

.jumbotron__list a:hover { text-decoration: none; color: #2d9c44; }

.breadcrumb { font-size: 12px; background: none; padding: 0; }

@media only screen and (min-width: 768px) { .breadcrumb { font-size: 1rem; } }

.breadcrumb li, .breadcrumb a { color: #eeeeee; }

.breadcrumb li.active { color: #cccccc; }

.product__item { position: relative; overflow: hidden; display: block; }

@media only screen and (min-width: 992px) { .product__item:not(.product__item--notransition):hover .product__title { transform: translateY(-2rem); }
  .product__item:not(.product__item--notransition):hover .product__title:after { transform: translateY(-1.5rem); } }

@media only screen and (min-width: 992px) { .product__item:not(.product__item--notransition):hover .product__nav { transform: translateY(0); } }

@media only screen and (min-width: 992px) { .product__item:not(.product__item--notransition):hover .product__overlay:after { transform: translateY(-100%); } }

.product__item--notransition .product__title { transform: translateY(0); font-size: 1.25rem; }

@media only screen and (min-width: 992px) { .product__item--notransition .product__title { font-size: 1.375rem; } }

.product__item--notransition .product__overlay:after { bottom: -20%; height: 50%; }

.product__item--notransition:hover .product__overlay:after { bottom: 0; }

.product__item--notransition:hover .product__title { transform: translateY(-1rem); }

.product__item--notransition:hover .product__title:after { display: none; }

.product__overlay { position: absolute; top: 0; left: 0; width: 100%; height: 100%; display: flex; flex-wrap: wrap; align-content: flex-end; align-items: flex-end; padding: 1.25rem; background: rgba(0, 0, 0, 0.28); }

.product__overlay:after { transition: all ease 0.3s; position: absolute; height: 100%; width: 100%; content: ' '; bottom: -100%; left: 0; background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#160604',GradientType=0 ); }

.product__title { color: #ffffff; font-weight: 700; text-transform: uppercase; width: 100%; transform: translateY(8rem); position: relative; z-index: 2; transition: all ease 0.3s; font-size: 1.25rem; }

@media only screen and (min-width: 992px) { .product__title { font-size: 1.5rem; } }

.product__title:after { position: absolute; background: #2d9c44; height: 3px; width: 3rem; content: ' '; left: 0; bottom: -2.5rem; z-index: 2; transition: all ease 0.3s; }

.product__nav { transform: translateY(20rem); margin: 0; height: 8rem; overflow: hidden; position: relative; z-index: 2; transition: all ease 0.3s; }

.product__nav li { margin-bottom: 0.125rem; color: #ffffff; }

.product__nav li:hover { text-decoration: underline; }

.portfolio__title { font-size: 1.25rem; font-weight: 700; }

.portfolio__link { text-decoration: none; color: #333333; transition: all ease 0.3s; }

.portfolio__link:hover { color: #2d9c44; text-decoration: none; }

.portfolio__link:hover img { opacity: 0.5; }

.portfolio__link img { display: block; -webkit-backface-visibility: hidden; line-height: 1; margin-bottom: 0.5rem; transition: opacity ease 0.3s; }

.callback__lay { background-image: url(../img/callback.jpg); padding-top: 4rem; padding-bottom: 3rem; }

.callback label { color: #ffffff; line-height: 1.3; margin-top: 0.25rem; font-size: 0.875rem; }

.callback__btn { background-color: #2d9c44; text-transform: uppercase; padding-left: 2rem; padding-right: 2rem; }

.project__lay { background-color: #fff; padding: 1rem 1rem 0 1rem; display: flex; flex-wrap: wrap; justify-content: space-between; }

.project__left { width: 100%; margin-bottom: 1rem; }

@media only screen and (min-width: 992px) { .project__left { width: 40%; } }

.project__right { width: 100%; margin-bottom: 1rem; }

@media only screen and (min-width: 992px) { .project__right { width: calc(100% - 40% - 2rem); } }

.project-slider__item { position: relative; }

.project-slider__overlay { position: absolute; height: 100%; width: 100%; top: 0; left: 0; display: flex; flex-wrap: wrap; align-content: flex-end; align-items: flex-end; padding: 1rem; border-bottom: 3px solid #2d9c44; }

.project-slider__overlay:before { position: absolute; bottom: 3px; left: 0; width: 100%; height: 50%; content: ' '; background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(22, 6, 4, 0.8) 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#160604',GradientType=0 ); }

.project-slider__title { font-size: 1.125rem; text-transform: uppercase; color: #ffffff; font-weight: 700; margin-bottom: 0.5rem; position: relative; z-index: 2; }

.project-slider__descr { color: #eeeeee; position: relative; z-index: 2; }

.content .table { background-color: #fff; }

.content .table th { vertical-align: top !important; }

.content .table td { vertical-align: middle; }

.content ul { margin-bottom: 1rem; margin-left: 1rem; list-style: none; }

.content ul li { margin-bottom: 0.25rem; }

.content ul li::before { content: " "; display: inline-block; width: 0.5rem; height: 0.5rem; background: #0c730e; border-radius: 50%; margin-left: -1em; margin-right: 0.75rem; }

.content h2, .content h3, .content h4, .content h5, .content h6 { color: #343a40; }

.content blockquote { padding: 1rem; border-left: 2px solid green; background: #fff; margin-bottom: 2rem; }

.content blockquote p:last-child { padding: 0; margin: 0; }

.footer { padding: 3rem 0 1.5rem; background-color: #1f2024; }

.footer__logo { max-width: 12rem; text-align: center; margin-bottom: 3rem; }

.footer__logo img { margin-bottom: 0.5rem; }

.footer__logo p { font-size: 0.875rem; line-height: 1.2; color: #ffffff; max-width: 13rem; }

.footer__title { font-size: 0.875rem; font-weight: 700; display: block; color: #ffffff; }

.footer__title:hover { color: #ffffff; }

.footer__nav { margin: 0.25rem 0 1.25rem; padding: 0; }

.footer__nav li a { color: #9e9e9e; font-size: 0.875rem; line-height: 1.3; display: inline-block; }

.copyright { padding: 1rem 0; background-color: #2a2c32; }

.copyright__phone { margin-left: 2rem; display: flex; flex-direction: column; justify-content: flex-end; align-items: flex-end; }

.copyright__phone a { color: #ffffff; font-size: 1.125rem; font-weight: 700; }

.copyright__phone span { font-size: 0.875rem; font-weight: 400; }

.copyright__adress { margin-left: 2rem; text-align: right; }

.copyright__adress p { font-size: 0.875rem; color: #ffffff; margin-bottom: 0; line-height: 1.6; }

.copyright__bank { margin-left: 2rem; }

.mobile { padding-top: 2rem; padding-bottom: 2rem; position: absolute; top: 0; left: 0; width: 100%; min-height: 100vh; z-index: 10; background-color: #383333; left: -150%; transition: all ease 0.3s; }

@media only screen and (max-width: 992px) { .mobile--is-open { transform: translateX(150%); } }

.mobile__nav li { margin-bottom: 0.25rem; }

.mobile__nav li a { font-size: 1.125rem; font-weight: 700; color: #ffffff; }

.mobile__phone { padding: 1rem; background-color: #484242; border-top: 2px solid #2d9c44; margin-top: 2rem; color: #ffffff; }

.mobile__phone a { color: #2d9c44; display: block; font-size: 1.125rem; color: #ffffff; font-weight: 700; }

.mobile__phone svg { margin-right: 0.5rem; }

.mobile__adress { margin-top: 2rem; padding: 1rem; border-top: 2px solid #2d9c44; background-color: #484242; color: #ffffff; }

.mobile__adress p { color: #ffffff; }

.mobile__adr { font-weight: 700; }

.mobile__adr svg { margin-right: 0.5rem; }

.mobile__close { position: absolute; color: #ffffff; right: 1.5rem; top: -0.5rem; font-size: 1.5rem; line-height: 1; }
